@import url(./fonts/lato.css);
@import url(./fonts/roboto.css);
@import url(./fonts/materialicons.css);
html {
  overflow-y: scroll; }

html, body, #root, #root > div, #root > div > main {
  height: 100%;
  min-width: 320px;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  min-height: 500px; }

@media screen and (min-width: 0px) and (max-width: 99999px) {
  #root, #root > div, #root > div > main,
  .sticky .header, .user-message-input.stick-down, .full-screen-container {
    max-width: 520px;
    margin: auto; } }

html {
  font-size: 10px; }

.luna-logo {
  height: 50px; }

a {
  color: inherit;
  text-decoration: none; }

body {
  font-size: 1.6rem;
  font-family: 'Lato', sans-serif;
  margin: 0 auto;
  text-align: center;
  background: #29133b;
  background-image: url("./../../images/background_logo.svg"), -webkit-gradient(linear, left top, right top, from(#0f022e), to(#4f124d));
  background-image: url("./../../images/background_logo.svg"), -webkit-linear-gradient(left, #0f022e, #4f124d);
  background-image: url("./../../images/background_logo.svg"), -o-linear-gradient(left, #0f022e, #4f124d);
  background-image: url("./../../images/background_logo.svg"), linear-gradient(90deg, #0f022e, #4f124d);
  background-position: 50% 50%, 0 0;
  background-size: cover, auto;
  background-repeat: repeat-y, repeat; }
  body .luna-app-main {
    background-color: white; }

.toastify-content--success {
  background-color: #70d79e !important; }

.toastify {
  word-break: break-word; }



