.button {
  font-family: "Lato", sans-serif;
  border-radius: 0.3rem;
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
  cursor: pointer;
  outline: none; }
  .button:hover {
    outline: none;
    text-decoration: none; }

.button-main {
  color: white;
  background-image: -webkit-gradient(linear, left top, right top, from(#603695), to(#862b91));
  background-image: -webkit-linear-gradient(left, #603695, #862b91);
  background-image: -o-linear-gradient(left, #603695, #862b91);
  background-image: linear-gradient(to right, #603695, #862b91); }

.button-no-border {
  border: none; }

.button-no-background {
  border: none;
  background: none; }



